.shadow-hover {
    transition: box-shadow 0.3s ease;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.4);
}

.shadow-hover:hover {
    box-shadow: 0 10px 25px rgba(0, 0, 0, 0.5);
    cursor: pointer;
}

.cards-wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
}

.card-item {
    flex: 0 1 calc(33.333% - 20px);
    /* 3 per riga, con un po' di margine */
    margin: 10px;
}

@media (max-width: 992px) {
    .card-item {
        flex: 0 1 calc(50% - 20px);
        /* 2 per riga */
    }
}

@media (max-width: 576px) {
    .card-item {
        flex: 0 1 calc(100% - 20px);
        /* 1 per riga */
    }
}

.image-text {
  background-image:
    linear-gradient(rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0.1)),
    url('/media/concerts/concerts.jpg');
}

#eventsAccordion .accordion-item.year-start {
  border-top: 1px solid var(--bs-accordion-border-color);
}

.accordion-button {
    color: #edddce; 
    transition: all ease-in-out 0.3s;
}

.accordion-button:hover {
    background-color: #111;
}

.accordion-button:not(.collapsed) {
  background-color: #111;      /* colore di sfondo */
  color: #edddce;                 /* colore testo */
  box-shadow: none;            /* rimuove glow blu */
}

.accordion-button:not(.collapsed)::after {
  filter: none;
  color: #edddce; 
}

.accordion-button::after {
  color: #edddce; 
}


.accordion-button:focus {
  box-shadow: none;
  border-color: transparent;
}

#eventsAccordion .accordion-item.month-start {
  border-top: 1px solid var(--bs-accordion-border-color);
}
